* {
  color: #f5f5f5;
  margin: 0;
}

body {
  background: #222;
  font-family: Roboto, sans-serif;
  font-size: 1.25rem;

  @media screen and (width <= 600px) {
    & {
      font-size: 1rem;
    }
  }
}

div {
  visibility: hidden;
  background: linear-gradient(60deg, #ff93ffa0, #2e095fa0);
  border-radius: 16px;
  padding: 20px;
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
}

#chosen-dices {
  top: 60%;
}

#roll-dice {
  background: linear-gradient(60deg, #ff93ffa0, #2e095fa0);
  border-radius: 16px;
  padding: 12px;
  font-size: larger;
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translateX(-50%);
}

#clear {
  background: linear-gradient(60deg, #ff93ffa0, #2e095fa0);
  border-radius: 16px;
  padding: 8px;
  position: absolute;
  top: 15px;
  left: 15px;
}
